import axios from "axios";
import { Message } from "element-ui";
import router from "@/router";
// import store from "@/store";
// import { getToken } from "@/utils/auth";

// create an axios instance
const service = axios.create({
  baseURL: process.env.BASE_API, // api 的 base_url
  timeout: 5000, // request timeout
  headers: { "X-Requested-With": "XMLHttpRequest" }
});

// request interceptor
service.interceptors.request.use(
  config => {
    // Do something before request is sent
    // if (store.getters.token) {
    //   // 让每个请求携带token-- ['X-Token']为自定义key 请根据实际情况自行修改
    //   config.headers["X-Token"] = getToken();
    // }
    return config;
  },
  error => {
    // Do something with request error
    console.log(error); // for debug
    Promise.reject(error);
  }
);

// response interceptor
service.interceptors.response.use(
  response => {
    const res = response.data;
    if (res.state == 100011000) {
      Message({
        message: res.data,
        type: "error",
        duration: 3 * 1000
      });
      return router.push({ name: "login" });
    }
    if (res.state !== 0) {
      let errMessage = res.data;
      if (Object.prototype.toString.call(errMessage) === "[object Object]") {
        errMessage = Object.keys(errMessage).reduce((acc, key) => {
          return errMessage[key].reduce((acc, obj) => {
            return Object.keys(obj).reduce(
              (acc1, key) =>
                `${acc1}<p style="line-height: 22px;">${obj[key]}</p>`,
              ""
            );
          }, "");
        }, "");
      }
      Message({
        message: errMessage,
        type: "error",
        duration: 3 * 1000,
        dangerouslyUseHTMLString: true
      });
      return Promise.reject("error");
    }
    return response.data;
  },
  error => {
    console.log("err" + error); // for debug
    let errMessage = error.message;
    if (error.response.status == "403") {
      errMessage = error.response.data.data;
    }
    Message({
      message: errMessage,
      type: "error",
      duration: 3 * 1000
    });
    return Promise.reject(error);
  }
);

export default service;
